/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { useState } from "react";
import { sendEmail } from "../services/validateForm";

const FormContact = () => {
  const [status, setStatus] = useState("");

  const submitForm = (event) => {
    sendEmail(event, setStatus);
  };

  return (
    <form
      action="https://formspree.io/xpzynper"
      method="POST"
      onSubmit={submitForm}
      className="form-contact"
    >
      <div className="field">
        <label className="label">Nombre</label>
        <div className="control">
          <input
            className="input"
            type="text"
            placeholder="Introduce tú nombre"
            name="name"
            required
          />
        </div>
      </div>

      <div className="field">
        <label className="label">Correo Electrónico</label>
        <div className="control has-icons-left">
          <input
            className="input"
            type="email"
            name="_replyto"
            placeholder="Introduce tú correo electrónico"
            required
          />
          <span className="icon is-small is-left">
            <i className="fas fa-envelope"></i>
          </span>
        </div>
      </div>

      <div className="field">
        <label className="label">Asunto</label>
        <div className="control">
          <input
            className="input"
            type="text"
            placeholder="Sobre que quieres hablar..."
            required
          />
        </div>
      </div>

      <div className="field">
        <label className="label">Mensaje</label>
        <div className="control">
          <textarea
            className="textarea"
            placeholder="Escribe tu mensaje aquí..."
            name="message"
            required
          ></textarea>
        </div>
      </div>

      <div className="field">
        <div className="control">
          <button className="button">Enviar</button>
        </div>
      </div>
      {status !== "" ? (
        status === "SUCCESS" ? (
          <div className="field notification is-info is-light">
            <p>
              Gracias por tu mensaje{" "}
              <span role="img" aria-label="smille">
                😃
              </span>{" "}
              me pondré en contacto contigo lo antes posible!!
            </p>
          </div>
        ) : (
          <div className="field notification is-danger is-light">
            <p>
              Parece que ha ocurrido un error{" "}
              <span role="img" aria-label="smille">
                😱
              </span>{" "}
              intentaré solucionarlo lo antes posible!!
            </p>
          </div>
        )
      ) : null}
    </form>
  );
};

export default FormContact;
